html {
  background-color: #ffffff;
  font-family: "Noto Sans JP", "Helvetica Neue", "Helvetica", "Hiragino Sans", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", sans-serif;
  font-size: 16px;
}

body {
  margin: 0;
  height: 100%;
}

h2 {
  font-size: 1.2rem;
}

.contentWrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.main {
  width: 80%;
  margin: auto;
}

.header {
  width: 100%;
  box-sizing: border-box;

  margin-bottom: 20px;
  padding: 5px 0;

  box-shadow: 0 3px 10px #00000040;

}

.header .headerBox {
  width: 80%;
  margin: 5px auto;

  display: flex;
  align-items: center;

  justify-content: left;
}

.headerBox .logoBox {
  display: flex;
  align-items: center;
  justify-content: left;
}

.header .logo {
  height: 40px;
}

.header .logoImg {
  height: 40px;
}

.title {
  font-size: 20px;
  font-weight: bold;
  margin-left: 15px;
}

.searchBox {
  display: flex;
  align-items: center;
  justify-content: end;
  width: 100%;
  flex-grow: 1;
}

.searchBox > * {
  display: inline-block;
  box-sizing: border-box;
  height: 1.8rem;
  border: 1px solid #a2908b;
  border-right: none;
  min-width: 0;
}

.searchBox > *:last-child {
  border-right: 1px solid #a2908b;
}

.searchBox .ruleSelect {
  border-left: none;
}

.searchBox .textInput {
  font-size: 1rem;
  flex-grow: 1;
  margin-left: 2rem;
  max-width: 20rem;
  padding-left: 0.3rem;
  background-color: #faf9f9;
}

.searchBox .tagSelect, .searchBox .ruleSelect {
  font-size: 0.85rem;
  background-color: #f0eae6;
}

.searchBox .textInput::placeholder {
  font-style: italic;
}

.searchBox .searchButton {
  display: flex;
  background-color: #014f41;
  color: white;
  border: none;
  border-radius: 0 5px 5px 0;
  width: 2rem;
  justify-content: center;
  align-items: center;
}

.footer {
  width: 100%;
  flex-grow: 1;
  overflow: hidden;
  box-sizing: border-box;

  margin-top: 50px;
  padding: 30px 0;

  box-shadow: 0 3px 10px #00000040 inset;

  text-align: center;
}

.charaList {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(70px, 1fr));
}

.charaList .charaLink {
  height: 60px;
  width: 60px;

  display: inline-flex;
  justify-content: center;
  align-items: center;

  margin: 5px;
  border: 2px solid #c0c0c0;
  font-size: 25px;
}

.charaList .charaLink:hover {
  background-color: #e0f3ef;
}

.charaList > a {
  color: initial;
  text-decoration: none;
}

.pageWrapper {
  display: flex;
  flex-flow: row;
}

.info {
  display: flex;
  flex-flow: column;
  width: 25%;
  margin: 0 30px 0 20px;
}

.info .moveLink {
  width: 100%;
  display: flex;
  justify-content: start;
  align-items: center;
  border-bottom: 1px solid #a0a0a0;
}

.moveLink .nextButton {
  border-left: 1px solid #a0a0a0;
}

.moveLink .prevButton, .moveLink .nextButton {
  flex-grow: 1;
}

.moveLink .prevButton > *, .moveLink .nextButton > * {
  display: inline-block;
  text-decoration: none;
  font-size: 1.2rem;
  color: #028069;
  width: 100%;
  height: 2rem;
  text-align: center;
  user-select: none;
}

.moveLink .prevButton > *:hover, .moveLink .nextButton > *:hover {
  background-color: #edf8f6;
}

.moveLink .prevButton.disabled > *, .moveLink .nextButton.disabled > * {
  background-color: #e0e0e0;
  color: #707070;
}

.info > .charaFrame {
  width: 100%;
  height: 150px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.info > .charaFrame > .mainChara {
  font-size: 100px;
}

.info .kanji {
  display: inline-block;
  width: 100%;
  font-size: 24px;
  text-align: center;
}

.phun, .searchBox .textInput::placeholder {
  font-family: "Phun-Sans", "PhunWrite", "Times New Roman", "YuMincho", "Hiragino Mincho ProN", "Yu Mincho", "MS PMincho", serif;
  font-weight: normal;
}

.dataTable {
  border-collapse: collapse;
  border-spacing: 0;
  margin-top: 0.5rem;
}

.dataTable * {
  border: 1px solid #a0a0a0;
  padding: 5px;
}

.dataTable th {
  background-color: #f5f5f5;
  width: 30%;
}

.langs {
  flex-grow: 1;
}

.langs > .langData > h2 {
  border-left: 5px solid #014f41;
  padding: 2px 0 2px 0.5em;
  margin: 30px 0 15px 0;
  
  font-size: 1.3rem;
}

.langs > .langData:first-child > h2 {
  margin-top: 15px;
}

.langs .langData .dataRow {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #a0a0a0;
  padding: 0;
  font-size: 1rem;
}

.langs .langData .dataRow .dataLabel {
  font-weight: bold;
  background-color: #dcf2ee;
  width: 5rem;
  padding: 5px 0 5px 7px;
  margin-right: 10px;
}

.langs > .langData > .dataEntry > p {
  margin: 0;
}

.langData .langLinks {
  width: 100%;
  padding: 0.5rem 0.3rem;
  padding-bottom: 0;
}

.langData .langLinks span {
  margin: 0 0.3rem;
}

.phun404 {
  font-size: 24px;
}

@media screen and (max-width: 750px) {
  .App {
    width: 90%;
  }
  .pageWrapper, .headerBox {
    flex-flow: column;
  }
  .header .headerBox {
    width: 95%;
    align-items: center;
  }
  .logoBox {
    margin-bottom: 1rem;
  }
  .searchBox {
    justify-content: center;
  }
  .searchBox .textInput {
    margin-left: 0;
  }
  .info {
    width: 100%;
    margin: 0;
  }
  .langs > .langData:first-child > h2 {
    margin-top: 30px;
  }
}

@font-face {
  font-family: "PhunWrite";
  src: url(https://kaeru2193.github.io/Phun-Resources/font/PhunWrite-Regular-latest.woff) format("woff");
}

@font-face {
  font-family: "Phun-Sans";
  src: url(https://kaeru2193.github.io/Phun-Resources/font/Phun-Sans/Phun-Sans-Rounded-Regular.woff) format("woff");
  font-weight: normal;
}

@font-face {
  font-family: "Phun-Sans";
  src: url(https://kaeru2193.github.io/Phun-Resources/font/Phun-Sans/Phun-Sans-Rounded-Bold.woff) format("woff");
  font-weight: bold;
}